.App {
  text-align: center;
  background-color: #1a1a2e;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: Arial, sans-serif;
}

.App h1 {
  margin-bottom: 20px;
  font-size: 2rem;
}

.wheel-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wheel-pointer {
  font-size: 2rem;
  color: #ff6b6b;
  z-index: 1;
  margin-bottom: -10px;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.5));
}

.wheel-canvas {
  border-radius: 50%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4), 0 0 60px rgba(255, 255, 255, 0.05);
}

.spin-button {
  margin-top: 24px;
  padding: 14px 48px;
  font-size: 1.2rem;
  font-weight: bold;
  border: none;
  border-radius: 50px;
  background: linear-gradient(135deg, #ff6b6b, #ee5a24);
  color: white;
  cursor: pointer;
  transition: transform 0.15s, box-shadow 0.15s;
  box-shadow: 0 4px 15px rgba(238, 90, 36, 0.4);
}

.spin-button:hover:not(:disabled) {
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(238, 90, 36, 0.6);
}

.spin-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.winner-display {
  margin-top: 20px;
  font-size: 1.5rem;
  padding: 12px 32px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  backdrop-filter: blur(4px);
}

/* Player Form */
.form-container {
  background: rgba(255, 255, 255, 0.08);
  padding: 40px;
  border-radius: 16px;
  width: 100%;
  max-width: 380px;
  backdrop-filter: blur(8px);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.form-container h2 {
  margin: 0 0 4px;
  font-size: 1.6rem;
}

.form-subtitle {
  margin: 0 0 24px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.95rem;
}

.form-field {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-bottom: 16px;
}

.form-field label {
  font-size: 0.85rem;
  font-weight: 600;
  margin-bottom: 6px;
  color: rgba(255, 255, 255, 0.8);
}

.form-field input {
  padding: 12px 14px;
  border: 2px solid rgba(255, 255, 255, 0.15);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.06);
  color: #fff;
  font-size: 1rem;
  outline: none;
  transition: border-color 0.2s;
}

.form-field input:focus {
  border-color: #ff6b6b;
}

.form-field input::placeholder {
  color: rgba(255, 255, 255, 0.3);
}

.form-error {
  color: #ff6b6b;
  font-size: 0.8rem;
  margin-top: 4px;
}

.form-submit {
  margin-top: 8px;
  width: 100%;
  padding: 14px;
  font-size: 1.1rem;
  font-weight: bold;
  border: none;
  border-radius: 50px;
  background: linear-gradient(135deg, #ff6b6b, #ee5a24);
  color: white;
  cursor: pointer;
  transition: transform 0.15s, box-shadow 0.15s;
  box-shadow: 0 4px 15px rgba(238, 90, 36, 0.4);
}

.form-submit:hover {
  transform: scale(1.03);
  box-shadow: 0 6px 20px rgba(238, 90, 36, 0.6);
}
